	.slider {
		margin-top: 0;
		&__container {
			padding-left: 0;
			padding-right: 0;
		}
		&__border-radius {
			border-radius: 0;
		}
		&__slide {
			height: 280px;
			&-content {
				max-width: 365px;
				padding-left: 25px;
				padding-right: 25px;
				.-title {
					font-size: 1.75rem;
					margin-bottom: 20px;
				}
				.-button {
					font-size: 1rem;
				}
				.-note {
					font-size: 0.85rem;
				}
			}
		}
		.pretty-slider__navigation {
			.-arrow {
				opacity: 0.5;
				&:first-child {
					left: 0;
					transform: translate(5px, -50%);
				}
				&:last-child {
					right: 0;
					transform: translate(-5px, -50%);
				}
			}
		}
	}
	/* /slider */

	/* catalog-screen */
	.catalog-screen {
		&__cats {
			display: block;
		}
		&__cat {
			display: block;
			border-radius: 12px;
			margin-bottom: 40px;
			&:hover {
				transform: none;
				box-shadow: none;
			}
			&:last-child {
				margin-bottom: 0;
			}
			.-img {
				height: 215px;
			}
			.-name {
				height: 65px;
				padding-bottom: 6px;
				padding-left: 20px;
				padding-right: 20px;
				br {
					display: none;
				}
			}
		}
	}
	/* /catalog-screen */

	/* product-page */
	.product-page {
		&__product-wrapper {
			border-radius: 16px;
			padding-top: 25px;
			padding-bottom: 40px;
		}
		&__product-container {
			display: block;
			.-quty-row {
				display: grid;
				grid-template-columns: 1fr 1fr;
				justify-items: start;
				column-gap: 25px;
			}
			.-button {
				display: block;
				text-align: center;
			}
		}
	}
	/* /product-page */

	/* info-screen */
	.info-screen {	
		&__container {
			display: block;
		}
		&__title {
			margin-bottom: 60px;
			font-size: 2rem;
			.-highlighting {
				.-outline {
					left: -10px;
					transform-origin: left top;
					transform: scale(.8);
				}
			}
		}
	}
	/* /info-screen */

	/* catalog-page */
	.catalog-page {
		&__wrapper.--products {
			gap: 15px;
		}
		&__product {
			border-radius: 8px;
			.-img {
				border-radius: 0 0 16px 16px;
			}
			.-name {
				font-size: 1.2rem;
			}
			.-price {
				font-size: 1.1rem;
			}
			.-button {
				font-size: 0.9rem;
			}
		}
	}
	/* /catalog-page */

	/* profile-page */
	.profile-tabs {
		border-radius: 24px;
		&__profile-tab {
			padding: 40px 20px 60px 20px;
			.-phone {
				margin-bottom: 40px;
			}
			.-form {
				display: block;
				margin-bottom: 40px;
				.-input {
					margin-bottom: 20px;
					.-grid {
						display: grid;
						grid-template-columns: repeat(3, 1fr);
						column-gap: 5px;
						input {
							display: block;
							width: 100% !important;
							&.--d {
								margin-right: 0;
							}
						}
					}
				}
			}
			.-addresses {
				.-button {
					max-width: none;
				}
			}
		}
		&__orders-tab {
			padding: 25px 15px 60px 15px;
			.-table {
				display: block;
				white-space: normal;
				max-width: 350px;
				margin: 0 auto;
				margin-bottom: 40px;
				thead {
					display: none;
				}
				tbody {
					display: block;
				}
				tr {
					display: block;
					margin-bottom: 20px;
					height: auto;
					background-color: #fff;
					border-radius: 16px;
					padding-top: 25px;
					padding-bottom: 35px;
					&:last-child {
						margin-bottom: 0;
					}
				}
				td {
					display: flex;
					justify-content: space-between;
					border-radius: 0 !important;
					background: none;
					padding: 0 20px;
					margin-bottom: 15px;
					&:last-child {
						margin-bottom: 0;
					}
					&.-icon {
						svg {
							display: none;
						}
					}
					.-prefix {
						display: block;
						font-weight: 700;
						margin-right: 20px;
					}
					.-value {
						text-align: right;
					}
				}
				.-address {
					.-very-cutted {
						display: none;
					}
					.-full {
						display: block; 
					}
				}
			}
		}
		&__bonuses-tab {
			padding: 40px 20px 60px 20px;
			position: relative;
			.-history {
				.-table-wrapper {
					height: auto;
					max-height: none;
				}
			}
		}
	}
