						flex-grow: 1;
						margin-right: 10px;
						.input-droplist {
							flex-grow: 1;
						}
					}
				}
			}
			.-radio-selector {
				display: block;
				.-radio {
					margin-bottom: 15px;
					justify-content: center;
					&:last-child {
						margin-bottom: 0;
					}
					.-name {
						width: 190px;
					}
				}
			}
			.-balance-section {
				.-split {
					display: block;
					.-side {
						display: block;
						&:first-child {
							margin-bottom: 30px;
						}
					}
				}
				.-input {
					width: auto;
				}
			}
			.-total-section {
				.-between {
					display: block;
					.-prefix {
						margin-bottom: 15px;
					}
				}
			}
		}
	}
	/* /cart-page */

	/* footer */
	.footer {
		&__container {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			gap: 10px;
		}
		&__logo {
			grid-column: 1;
			margin-bottom: 80px;
		}
		&__menu {
			grid-column: 1;
			&.--fm-01 {
				grid-row: 2;	
			}
			&.--fm-02 {
				grid-row: 3;	
			}
		}
		&__button {
			grid-column: 2;
			align-self: start;
			padding-top: 30px;
		}
		&__stores {
			grid-column: 1/3;
			grid-row: 5;
			padding: 60px 0;
		}
		&__schedule {
			grid-column: 2;
			&.--fs-01 {
				grid-row: 2;	
			}
			&.--fs-02 {
				grid-row: 3;	
			}
		}
		&__socials {
			padding-top: 40px;
			grid-column: 1;
			grid-row: 4;
		}
		&__phone {
			grid-row: 4;
			grid-column: 2;
			align-self: end;
			padding-bottom: 8px;
		}
		&__copyright {
			grid-row: 6;
			grid-column: 1/3;
		}
	}
	/* /footer */

}

@media(max-width: 600px) {
	.modals {
		&__modals {
			padding-top: 30px;
		}
		&__close {
			top: 15px;
			right: auto;
			left: 50%;
			transform: translateX(-50%);
		}
		&__title {
			font-size: 1.7rem;
		}
		&__modal {
			padding-left: 20px;
			padding-right: 20px;
			padding-top: 50px;
			&.--mini {
				padding-top: 25px;
			}
		}
		&__sms-modal {
			.-input {
				margin-top: 30px;
			}
		}
		&__tel-modal {
			padding-bottom: 0;
		}
		&__address-modal {
			padding-top: 0;
			.-grid-labels {
				column-gap: 15px;
			}
			.-button {
				margin-top: 35px;
			}
			.-addresses-wrapper {
				.-addresses {
					max-height: 150px;
					li {
						border-bottom: 1px solid #E0E0E0;
					}
				}
			}
		}
	}

	/* catalog-page */
	.catalog-page {
		padding-top: 20px;
		&__wrapper {
			display: block;
			&.--products {
				display: grid;
			}
		}
		&__cat {
			display: block;
			margin-bottom: 40px;
			&:last-child {
				margin-bottom: 0;
			}
			.-name {
				display: block;
				height: auto;
				padding-top: 15px;
				padding-bottom: 20px;
			}
		}
		&__product {
			.-weight {
				padding-top: 4px;
			}
		}
	}
	/* /catalog-page */

	/* product-page */
	.product-page {
		&__product-wrapper {
			padding-left: 25px;
			padding-right: 25px;
		}
	}
	/* /product-page */
}

