	}

	.profile-tabs {
		border-radius: 0;
		&__content {
			padding-top: 70px;
			min-height: auto;
		}
		&__header {
			display: flex;
			flex-direction: column;
			gap: 0;
			position: absolute;
			left: 0;
			right: 0;
			z-index: 2;
			overflow: hidden;
			&.active {
				border-radius: 0 0 16px 16px;
				.-tab {
					display: flex;
				}
			}
			.-hover-toggler {
				position: absolute;
				left: 0;
				right: 0;
				top: 0;
				height: 70px;
				z-index: 3;
			}
			.-tab {
				height: 70px;
				border-bottom: 2px solid #eee;
				display: none;
				&:last-child {
					border-bottom: none;
				}
				&.active {
					display: flex;
					order: -1;
					border-bottom: 2px solid #eee;
					.-arrow {
